<template>
    <div id="Buttons">
                <h3 class="margin-bottom10"></h3>
                <div class="padding-10">
                    <Button></Button>
                    <Button type="primary" size="large"></Button>
                    <Button type="dashed" size="small"></Button>
                    <Button type="text"></Button>
                    <br><br>
                    <Button type="info"></Button>
                    <Button type="success"></Button>
                    <Button type="warning"></Button>
                    <Button type="error"></Button>
                </div>
                <h3 class="margin-bottom10 margin-top10">ghost</h3>
                <div class="ghost-back padding-10">
                    <Button ghost></Button>
                    <Button type="primary" size="large" ghost></Button>
                    <Button type="dashed" size="small" ghost></Button>
                    <Button type="text" ghost></Button>
                    <br><br>
                    <Button type="info" ghost></Button>
                    <Button type="success" ghost></Button>
                    <Button type="warning" ghost></Button>
                    <Button type="error" ghost></Button>
                </div>
                <h3 class="margin-bottom10 margin-top10">disabled</h3>
                <div class="padding-10">
                    <Button type="info" disabled></Button>
                    <Button type="success" disabled></Button>
                    <Button type="warning" disabled ghost></Button>
                    <Button type="error" disabled ghost></Button>
                </div>
                <h3 class="margin-bottom10 margin-top10"></h3>
                <div class="padding-10">
                    <Button type="primary" icon="ios-search"></Button>
                    <Button type="primary" shape="circle" icon="ios-search"></Button>
                    <Button type="primary" disabled icon="ios-search"></Button>
                </div>
    </div>
</template>

<script>
    export default {
        name: 'Buttons'
    }
</script>

<style scoped>

</style>



