<template>
    <div id="iView">
        <h3 class="margin-bottom10"></h3>
        <div class="padding-10">
            <div class="dis-inline">
                <CWDropDown :dropBack="lists" @menuDrop="selectMenu" type="primary"></CWDropDown>
            </div>
            <div class="dis-inline">
                <CWDropDown :dropBack="lists" :search="true" class="margin-top10"></CWDropDown>
            </div>
        </div>
        <h3 class="margin-bottom10 margin-top10"></h3>
        <div class="padding-10">
            <ButtonGroup class="CW-group-buttons">
                <Button type="info">1</Button>
                <Button type="info">2</Button>
                <Button type="info">3</Button>
            </ButtonGroup>
            <ButtonGroup class="margin-top10 CW-group-ghost">
                <Button type="info" ghost>1</Button>
                <Button type="info" ghost>2</Button>
                <Button type="info" ghost>3</Button>
            </ButtonGroup>
        </div>
        <h3 class="margin-bottom10 margin-top10"></h3>
        <div class="padding-10">
            <Tabs value="name1" :animated="false">
                <TabPane label="" name="name1"></TabPane>
                <TabPane label="" name="name2"></TabPane>
                <TabPane label="" name="name3"></TabPane>
            </Tabs>
        </div>
        <div class="padding-10">
            <Tabs type="card" :animated="false">
                <TabPane label="" name="name1"></TabPane>
                <TabPane label="" name="name2"></TabPane>
                <TabPane label="" name="name3"></TabPane>
            </Tabs>
        </div>
    </div>
</template>

<script>


    export default {
        name: 'iView',
        data() {
            return {
                lists: [
                    {
                        name: '1',
                        disable: false,
                    },
                    {
                        name: '2',
                        disable: false,
                    },
                    {
                        name: '3',
                        disable: true,
                    },
                    {
                        name: '4',
                        disable: false,
                    },
                ],
                modal1: false
            }
        },
        mounted() {
        },
        methods: {
            selectMenu(data) {
                console.log(data);
            }
        }
    }
</script>

<style scoped lang="scss">
    #iView {
        padding-top: 10px;

        .ghost-back {
            background: $split-color;
        }
    }
</style>



