import Vue from 'vue';
import Vuex from 'vuex';
import * as getters from './getters'
import * as actions from './actions'
import * as mutations from './mutations'

Vue.use(Vuex);
const state = { //state
    initial: 'initial'
    //
};
// 
const store = new Vuex.Store({
        state, // state
        getters, // 
        mutations, // 
        actions, // state
    })
;

export default store;



